* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body {
  font-family: "Geometria";
  font-weight: normal;
}

section:not(:first-child) {
  margin-top: 100px;
}

.container {
  max-width: 1110px;
  margin: 0 auto;
}

.title {
  font-weight: 800;
  font-size: 36px;
  line-height: 45px;
  text-transform: uppercase;
  color: #82b428;
}

.title--blue {
  color: #2da3dc;
}

.text {
  font-size: 24px;
  color: #2b2422;
}

.button {
  padding: 11px 14px 13px;
  font-size: 20px;
  color: #fff;
  border-radius: 50px;
  border: none;
  cursor: pointer;
}

.hero {
  background-image: url("../../dist/images/heroImg.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  padding: 125px 0 172px 0;
}
.hero h1 {
  max-width: 520px;
  margin-top: 35px;
  font-size: 48px;
  position: relative;
}
.hero p {
  max-width: 403px;
  margin-top: 65px;
  line-height: 110%;
}
.hero button {
  width: 275px;
  height: 51px;
  margin-top: 21px;
  background: linear-gradient(34.66deg, #bc1822 16.88%, #e53322 76%);
  box-shadow: 3px 3px 15px rgba(0, 0, 0, 0.2);
  border-radius: 50px;
}
.hero .circle {
  background: linear-gradient(147.64deg, #ffffff 13.56%, #f1f1f1 85.54%);
  width: 664px;
  height: 664px;
  border-radius: 100%;
  padding: 105px 0 78px 145px;
  position: relative;
  left: -145px;
}
.hero .circle::before {
  content: "";
  width: 268px;
  height: 268px;
  background: linear-gradient(147.64deg, #c5d210 13.56%, #82b428 85.54%);
  position: absolute;
  border-radius: 100%;
  top: -48px;
  right: -110px;
}
.hero .circle::after {
  content: "";
  width: 105px;
  height: 105px;
  background: linear-gradient(147.64deg, #8dceed 13.56%, #2da3dc 85.54%);
  position: absolute;
  border-radius: 100%;
  right: -210px;
  top: 220px;
}

.attraction .wrapper {
  display: flex;
  justify-content: space-between;
}
.attraction .title--blue {
  color: #2da3dc;
}
.attraction h3 {
  max-width: 387px;
  line-height: 45px;
}
.attraction p {
  margin-top: 45px;
  max-width: 420px;
  line-height: 120%;
}
.attraction img {
  max-height: 400px;
}

.features .cards {
  margin-top: 50px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 30px;
}
.features .card {
  position: relative;
  cursor: pointer;
}
.features p {
  max-width: 280px;
  position: absolute;
  bottom: 32px;
  left: 28px;
  font-size: 24px;
  font-weight: 500;
  line-height: 100%;
  color: #ffffff;
  z-index: 2;
}
.features .cardShadow {
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, #000000 100%);
  background-blend-mode: multiply;
  border-radius: 0px 0px 10px 10px;
  position: absolute;
  bottom: 0;
  z-index: 1;
  width: 100%;
  height: 148px;
  left: 0;
  z-index: 1;
}

.flats .carousel {
  margin-top: 60px;
}
.flats .carousel-item {
  margin-right: 50px;
  width: 1140px;
  min-height: 550px;
  background: #ffffff;
  border: 1px solid #c5c5c5;
  box-shadow: 8px 8px 25px rgba(0, 0, 0, 0.25);
  border-radius: 20px;
  display: flex;
  position: relative;
  left: 20px;
}
.flats .carousel .item-image {
  width: 360px;
  height: 560px;
  position: relative;
}
.flats .carousel .item-content {
  padding: 70px 52px 0 0;
  display: flex;
  flex-direction: column;
}
.flats .carousel .item-content .plan p {
  text-align: center;
  font-style: italic;
  font-weight: 400;
  font-size: 17px;
  line-height: 23px;
  color: #000000;
}
.flats .carousel .item-content .plan-description {
  margin-left: 40px;
}
.flats .carousel .item-content .plan-description h6 {
  font-size: 24px;
  color: #2b2422;
}
.flats .carousel .item-content .plan-description p {
  max-width: 310px;
  margin-top: 45px;
  font-size: 17px;
  line-height: 135%;
  color: #000000;
}
.flats .carousel .item-content .plan-description button {
  margin-top: 23px;
  padding: 11px 22px;
  background: linear-gradient(147.64deg, #c5d210 13.56%, #82b428 85.54%);
}
.flats .plan-wrapper {
  display: flex;
  position: relative;
}
.flats .content-wrapper .flat-description {
  margin: 40px 65px 70px 45px;
  display: flex;
  justify-content: space-between;
  position: relative;
}
.flats .content-wrapper .flat-description::before {
  content: "";
  position: absolute;
  width: 590px;
  height: 1px;
  background-color: #000000;
  top: -20px;
}
.flats .content-wrapper .flat-description .name {
  color: #2b2422;
  font-weight: 400;
  font-size: 17px;
  line-height: 135%;
}
.flats .content-wrapper .flat-description .number {
  color: #2b2422;
  font-weight: 800;
  font-size: 22px;
  line-height: 28px;
}

.slick-dots {
  display: flex;
  justify-content: center;
  list-style: none;
}
.slick-dots button {
  font-size: 0;
  width: 12px;
  height: 12px;
  border-radius: 100%;
  border: none;
  color: #9c9c9c;
  margin-right: 18px;
  cursor: pointer;
}
.slick-dots .slick-active button {
  background-color: #2da3dc;
}

.slick-list {
  height: 600px;
}

.catalog .cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 30px;
}
.catalog .card {
  position: relative;
  cursor: pointer;
}
.catalog .card img {
  border-radius: 10px;
  background-color: rgba(0, 0, 0, 0.3);
}
.catalog p {
  max-width: 280px;
  position: absolute;
  bottom: 32px;
  left: 28px;
  font-size: 24px;
  font-weight: 500;
  line-height: 100%;
  color: #ffffff;
  z-index: 2;
}
.catalog .cardShadow {
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, #000000 100%);
  background-blend-mode: multiply;
  border-radius: 0px 0px 10px 10px;
  position: absolute;
  bottom: 0;
  z-index: 1;
  width: 100%;
  height: 148px;
  left: 0;
  z-index: 1;
}
.catalog .catalog-into {
  position: relative;
}
.catalog .catalog-into img {
  max-width: 1110px;
}
.catalog h3 {
  max-width: 245px;
  color: #646865;
  position: absolute;
  top: 99px;
  right: 90px;
}
.catalog button {
  background: linear-gradient(147.64deg, #c5d210 13.56%, #82b428 85.54%);
  position: absolute;
  right: 90px;
  bottom: 45px;
}

.details h2 {
  font-size: 48px;
  text-align: center;
  color: #2da3dc;
  max-width: 644px;
  margin: 0 auto;
  line-height: 60px;
}
.details .cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 30px;
  margin-top: 44px;
  gap: 28px;
}
.details .card {
  position: relative;
  cursor: pointer;
}
.details p {
  max-width: 280px;
  position: absolute;
  bottom: 32px;
  left: 28px;
  font-size: 24px;
  font-weight: 500;
  line-height: 100%;
  color: #ffffff;
  z-index: 2;
}
.details .cardShadow {
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, #000000 100%);
  background-blend-mode: multiply;
  border-radius: 0px 0px 10px 10px;
  position: absolute;
  bottom: 0;
  z-index: 1;
  width: 100%;
  height: 148px;
  left: 0;
  z-index: 1;
}
.details button {
  font-weight: 800;
  font-size: 24px;
  text-align: center;
  color: #c5c5c5;
  margin: 40px auto 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
  display: block;
  transition: 0.3s all;
}
.details button:hover {
  color: #989898;
}

.discounts {
  margin: 100px auto 0;
  min-height: 470px;
  padding: 62px 0 69px 0;
  background-image: url("../../dist/images/discounts-bg.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}
.discounts h3 {
  color: #2da3dc;
  max-width: 250px;
}
.discounts p {
  margin-top: 34px;
  max-width: 442px;
}
.discounts .button {
  background: linear-gradient(147.64deg, #c5d210 13.56%, #82b428 85.54%);
  margin-top: 46px;
}

.form {
  background-image: url("../../dist/images/form-bg.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  min-height: 388px;
  padding: 87px 0 77px 0;
}
.form .wrapper {
  display: flex;
  justify-content: space-between;
}
.form h3 {
  max-width: 400px;
}
.form p {
  color: #fff;
  max-width: 400px;
  line-height: 110%;
  margin-top: 32px;
}
.form form {
  display: flex;
  flex-direction: column;
}
.form input {
  width: 562px;
  height: 62px;
  background: #ffffff;
  border: 1px solid #c4c4c4;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
  border-radius: 50px;
  margin-top: 11px;
  padding: 18px 29px;
  font-size: 26px;
  color: #2b2422;
}
.form input:focus {
  outline: none;
}
.form input::placeholder {
  color: #c4c4c4;
}
.form button {
  width: 275px;
  background: linear-gradient(147.64deg, #c5d210 13.56%, #82b428 85.54%);
  margin-top: 23px;
  align-self: flex-end;
}

footer {
  background-color: #2da3dc;
  min-height: 305px;
  padding: 58px 0 52px 0;
}
footer .wrapper {
  display: flex;
  justify-content: space-between;
}
footer .social {
  width: 184px;
  display: flex;
  justify-content: space-between;
}
footer .inner {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
footer .telephone {
  text-decoration: none;
  font-weight: 700;
  font-size: 30px;
  line-height: 27px;
  color: #ffffff;
  margin-top: 22px;
}
footer .polite {
  margin-top: 38px;
  color: #fff;
  text-decoration: none;
  font-size: 12px;
}
footer p {
  font-size: 12px;
  line-height: 0px;
  text-align: center;
  color: #ffffff;
  max-width: 850px;
  margin-top: 35px;
}
footer p:nth-child(even) {
  margin-top: 20px;
}
footer img {
  max-width: 230px;
  max-height: 70px;
}